import * as S from "./style";
import { css } from "@emotion/react";
interface CategoryProps {
  menu: string[];
}

const Category = ({ menu }: CategoryProps) => {
  return (
    <S.Category>
      <S.MenuWrap>
        {menu.map((i) => {
          return (
            <div
              key={i}
              css={css`
                display: flex;
                flex-direction: column;
              `}
            >
              <S.Dot />
              <S.Menu key={i}>{i}</S.Menu>
            </div>
          );
        })}
      </S.MenuWrap>
    </S.Category>
  );
};

export default Category;
